<template>
  <div id="tablet">
    <div class="queryBox">
      <span class="title"><i class="iconfont icon-shenhe1"></i>任务成果审核详情</span>
      <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="backHistory">返回</Button>
    </div>
    <div class="content_box">
      <div>
        <Form ref="taskEditForm" :model="achievementData" :label-width="110">
          <Row>
            <Col span="6" offset="6">
              <FormItem prop="cd004" label="台账编码：">
                <Input v-model.trim="achievementData.cd004" size="default" :title="achievementData.cd004" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck005" label="所在功能区：">
                <Input size="default" v-model.trim="achievementData.ck005" :title="achievementData.ck005" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck007" label="经度：">
                <Input size="default" v-model.trim="achievementData.ck007" :title="achievementData.ck007" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck008" label="纬度：">
                <Input size="default" v-model.trim="achievementData.ck008" :title="achievementData.ck008" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck011" label="占地面积(km²)：">
                <Input size="default" v-model.trim="achievementData.ck011" :title="achievementData.ck011" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck010" label="活动/设施名称：">
                <Input size="default" v-model.trim="achievementData.ck010" :title="achievementData.ck010" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck016" label="活动/设施类型：">
                <Input size="default" v-model.trim="achievementData.ck016" :title="achievementData.ck016" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck012" label="活动/设施现状：">
                <Input size="default" v-model.trim="achievementData.ck012" :title="achievementData.ck012" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck019" label="建设单位：">
                <Input size="default" v-model.trim="achievementData.ck019" :title="achievementData.ck019" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck043" label="整改进展：">
                <Input size="default" v-model.trim="achievementData.ck043" :title="achievementData.ck043" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck040" label="处罚方式：">
                <Input size="default" v-model.trim="achievementData.ck040" :title="achievementData.ck040" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck041" label="罚款(万元)：">
                <Input size="default" v-model.trim="achievementData.ck041" :title="achievementData.ck041" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck047" label="核查单位 ：">
                <Input size="default" v-model.trim="achievementData.ck047" :title="achievementData.ck047" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck048" label="核查时间：">
                <Input size="default" v-model.trim="achievementData.ck048" :title="achievementData.ck048" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck049" label="核查人：">
                <Input size="default" v-model.trim="achievementData.ck049" :title="achievementData.ck049" disabled/>
              </FormItem>
            </Col>
            <Col span="6" offset="6">
              <FormItem prop="ck050" label="核查人联系方式：">
                <Input size="default" v-model.trim="achievementData.ck050" :title="achievementData.ck050" disabled/>
              </FormItem>
            </Col>
            <Col span="18" offset="6" style="width: 65%">
              <FormItem prop="ck076" label="备注：">
                <i-input v-model.trim="achievementData.ck076" type="textarea" :autosize="{minRows: 5}" :title="achievementData.ck076" disabled></i-input>
              </FormItem>
            </Col>
          </Row>
        </Form>
        <p>
          <Button type="primary" @click="auditModal = true" v-if="auditStatus">审核</Button>
          <Button type="primary" @click="auditModal = true" v-else disabled>审核</Button>
        </p>
      </div>
    </div>
    <!--拒绝审核理由弹框 -->
    <Modal
      v-model="auditModal"
      @on-cancel="handleCancel">
      <div>
        <Form :model="auditData" ref="statusForm" :label-width="90" style="padding:0 50px;margin-top: 40px" :rules="DataRule" >
          <FormItem prop="ck070" label="审核意见：">
            <Input type="textarea" v-model.trim="auditData.ck070" placeholder="请输入审核意见" size="default"/>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button type="primary" @click="handleAudit" style="margin-right: 10px">审核通过</Button>
        <Button @click="cancelAudit">审核拒绝</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import {
  // 审核
  pointStageExamine
} from '@/api/achievement'
export default {
  name: 'achievementDetails',
  data () {
    return {
      // 编辑数据
      achievementData: {},
      // 审核意见
      auditModal: false,
      auditData: {
        ck070: ''
      },
      // 审核状态--是否已审核
      auditStatus: true,
      // 审核意见校验
      DataRule: {
        ck070: [
          { required: true, message: '审核意见不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  props: {
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  methods: {
    /* 编辑框默认值数据处理 */
    dataHandle () {
      // this.editForm = Object.assign({}, JSON.parse(sessionStorage.getItem('row')))
      this.achievementData = this.data
      if (this.data.ck067 === 0) {
        this.auditStatus = true
      } else if (this.data.ck067 === 1) {
        this.auditStatus = false
      } else if (this.data.ck067 === 2) {
        this.auditStatus = true
      } else {
        this.auditStatus = true
      }
    },
    // 审核通过
    handleAudit () {
      this.$refs.statusForm.validate(valid => {
        if (valid) {
          pointStageExamine({
            data: {
              ck001: this.data.ck001,
              ck067: '1',
              ck070: this.auditData.ck070
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '审核通过' })
              this.handleCancel()
            } else {
              this.$Notice.error({ title: res.data.msg })
              this.handleCancel()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            this.handleCancel()
          })
        }
      })
    },
    // 审核拒绝
    cancelAudit () {
      this.$refs.statusForm.validate(valid => {
        if (valid) {
          pointStageExamine({
            data: {
              ck001: this.data.ck001,
              ck067: '2',
              ck070: this.auditData.ck070
            }
          }).then((res) => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '审核拒绝' })
              this.handleCancel()
            } else {
              this.$Notice.error({ title: res.data.msg })
              this.handleCancel()
            }
          }).catch(() => {
            this.$Notice.error({ title: '服务器错误' })
            this.handleCancel()
          })
        }
      })
    },
    handleCancel () {
      this.auditData = {}
      this.auditModal = false
    },
    // 返回前一页
    backHistory () {
      this.$emit('signOut', false)
      this.$emit('getTableList', false)
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    height: 100%;
    overflow: hidden;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px;
      position: relative;
      overflow: inherit;
      line-height: 50px;
      text-align: right;
      .title {
        float: left;
        font-size: 14px;
        i {
          vertical-align: middle;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
    .content_box {
      height: calc(100% - 50px);
      overflow: hidden;
      &>div {
        padding: 20px 15px;
        height: 100%;
        overflow: auto;
      }
      .ivu-col-offset-6 {
        margin-left: 15%;
      }
    }
  }
</style>
